$offwhite: #f1f1f1;
$darkblue: #062758;
$hover: #1b4d97;

$color-main: #1f1f1f;
$background-color: #f5dba4;


body {
  margin: 0;
  padding: 0;
  background-color: $background-color;
}

nav {
  background-color: $darkblue;
  padding: 10px;
  ul{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    li{
      list-style: none;
      a {
        text-decoration: none;
        color: $offwhite;
      }
      a:hover {
        color: $offwhite;
        border-bottom: 2px solid $offwhite;
      }
    }
  }
}

#page-body {
  text-align: center;
  width: 80%;
  margin: 0 auto;
}

.article {
  text-align: left;
  a {
    text-decoration: none;
    color: $color-main;
  }
  a:hover {
    color: $hover;
  }
  h3 {
    font-size: 1.5rem;
    border-bottom: 2px solid $color-main;
  }
  p{
    padding: 0 20px 0 20px;
  }
}

#upvote-section{
  width: 50%;
  margin: 0 auto;
  p{
    color: $hover;
    text-align: center;
    padding: 5px;
  }
  button {
    float: left;
    background-color: $darkblue;
    color: $offwhite;
    padding: 5px 10px 5px 10px;
    border-style: none;
    border-radius: 5px;
    font-size: 15px;
  }
}

.article-content {
  p {
    text-align: left;
  }
}

#add-comment-form{
  display: flex;
  flex-direction: column;
  width: 50%;
  margin: 0 auto;
  label{
    padding: 5px;
  }
  input{
    padding: 5px;
    margin-left: 10px;
  }
  textarea {
    margin-left: 10px;
  }
  button {
    background-color: $darkblue;
    color: $offwhite;
    padding: 10px;
    border-style: none;
    border-radius: 5px;
    font-size: 15px;
    width: 200px;
    margin: 0 auto;
  }
}

h2,h3,h4,h5,h6 {
  color: $hover;
}

.comment-section{
  width: 50%;
  margin: 0 auto;
  text-align: left;
  h3{
    text-align: center;
  }
}